"use client"

import type React from "react"
import { createClient } from "@/lib/supabase/client"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { useRouter } from "next/navigation"
import { useEffect, useState } from "react"
import { AlertCircle, Check, Loader } from "lucide-react"

const COUNTRIES = [
  "Brasil",
  "Portugal",
  "EUA",
  "Canadá",
  "Argentina",
  "Chile",
  "Colômbia",
  "México",
  "Espanha",
  "Reino Unido",
  "Alemanha",
  "França",
  "Outros",
]

export default function ProfilePage() {
  const router = useRouter()
  const [loading, setLoading] = useState(true)
  const [saving, setSaving] = useState(false)
  const [successMessage, setSuccessMessage] = useState("")
  const [error, setError] = useState<string | null>(null)

  const [formData, setFormData] = useState({
    full_name: "",
    whatsapp_number: "",
    country_code: "+55",
    city: "",
  })

  const [preferences, setPreferences] = useState({
    job_area: "",
    country: "",
  })

  useEffect(() => {
    const fetchUserData = async () => {
      try {
        const supabase = createClient()

        const {
          data: { user },
          error: authError,
        } = await supabase.auth.getUser()

        if (authError) {
          console.error("[v0] Erro de autenticação:", authError.message)
          router.push("/auth/login")
          return
        }

        if (!user) {
          router.push("/auth/login")
          return
        }

        const { data: userData, error: userError } = await supabase
          .from("users")
          .select("*")
          .eq("id", user.id)
          .maybeSingle()

        if (userError) {
          console.error("[v0] Erro ao buscar dados do usuário:", userError)
          // Não redirecionar, apenas continuar com dados vazios
        }

        if (userData) {
          setFormData({
            full_name: userData.full_name || "",
            whatsapp_number: userData.whatsapp_number || "",
            country_code: userData.country_code || "+55",
            city: userData.city || "",
          })
        }

        setLoading(false)
      } catch (err) {
        console.error("[v0] Erro geral ao buscar dados:", err)
        setLoading(false)
      }
    }

    fetchUserData()
  }, [router])

  const handleInputChange = (field: string, value: string) => {
    setFormData((prev) => ({
      ...prev,
      [field]: value,
    }))
  }

  const handlePreferenceChange = (field: string, value: string) => {
    setPreferences((prev) => ({
      ...prev,
      [field]: value,
    }))
  }

  const handleSaveProfile = async (e: React.FormEvent) => {
    e.preventDefault()
    setSaving(true)
    setError(null)
    setSuccessMessage("")

    const supabase = createClient()

    try {
      const {
        data: { user },
        error: authError,
      } = await supabase.auth.getUser()

      if (authError || !user) {
        setError("Sessão expirada. Por favor, faça login novamente.")
        router.push("/auth/login")
        setSaving(false)
        return
      }

      console.log("[v0] Salvando perfil:", { user_id: user.id, ...formData })

      const { error: updateError } = await supabase.from("users").upsert(
        {
          id: user.id,
          email: user.email || "",
          full_name: formData.full_name,
          whatsapp_number: formData.whatsapp_number,
          country_code: formData.country_code,
          city: formData.city,
          updated_at: new Date().toISOString(),
        },
        { onConflict: "id" },
      )

      if (updateError) {
        console.error("[v0] Erro ao atualizar perfil:", updateError.message)
        throw new Error(updateError.message)
      }

      setSuccessMessage("Perfil atualizado com sucesso!")
      setTimeout(() => setSuccessMessage(""), 3000)
    } catch (err) {
      console.error("[v0] Erro capturado:", err)
      const errorMsg = err instanceof Error ? err.message : "Erro ao salvar perfil"
      setError(errorMsg)
    } finally {
      setSaving(false)
    }
  }

  const handleSavePreferences = async (e: React.FormEvent) => {
    e.preventDefault()
    setSaving(true)
    setError(null)
    setSuccessMessage("")

    if (!preferences.job_area || !preferences.country) {
      setError("Preencha todos os campos de preferência")
      setSaving(false)
      return
    }

    const supabase = createClient()

    try {
      const {
        data: { user },
        error: authError,
      } = await supabase.auth.getUser()

      if (authError || !user) {
        setError("Sessão expirada. Por favor, faça login novamente.")
        router.push("/auth/login")
        setSaving(false)
        return
      }

      console.log("[v0] Salvando preferências:", { user_id: user.id, ...preferences })

      // Usar upsert para evitar erro de constraint única
      const { error: prefError, data: prefData } = await supabase
        .from("job_preferences")
        .upsert(
          {
            user_id: user.id,
            job_area: preferences.job_area.trim(),
            country: preferences.country.trim(),
            updated_at: new Date().toISOString(),
          },
          { onConflict: "user_id,job_area,country" },
        )
        .select()
        .single()

      if (prefError) {
        console.error("[v0] Erro ao salvar preferências:", {
          message: prefError.message,
          details: prefError.details,
          hint: prefError.hint,
          code: prefError.code,
        })
        throw new Error(prefError.message || "Erro ao salvar preferências")
      }

      // Verificar se foi criado novo ou atualizado existente
      const isNew = prefData && new Date(prefData.created_at).getTime() > Date.now() - 1000
      setSuccessMessage(isNew ? "Preferência adicionada com sucesso!" : "Preferência atualizada com sucesso!")
      setPreferences({ job_area: "", country: "" })
      setTimeout(() => setSuccessMessage(""), 3000)
    } catch (err) {
      console.error("[v0] Erro capturado:", err)
      const errorMsg = err instanceof Error ? err.message : "Erro ao salvar preferências"
      setError(errorMsg)
    } finally {
      setSaving(false)
    }
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-slate-950 via-slate-900 to-slate-950 flex items-center justify-center">
        <Loader className="w-8 h-8 text-blue-400 animate-spin" />
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-950 via-slate-900 to-slate-950">
      <div className="px-4 sm:px-6 lg:px-8 py-8">
        <div className="max-w-4xl mx-auto space-y-8">
          {/* Header */}
          <div>
            <h1 className="text-3xl font-bold text-white">Meu Perfil</h1>
            <p className="text-slate-400 mt-2">Configure suas informações e preferências de emprego</p>
          </div>

          {/* Profile Form */}
          <Card className="border-slate-700 bg-slate-900">
            <CardHeader>
              <CardTitle className="text-xl text-white">Informações Pessoais</CardTitle>
              <CardDescription className="text-slate-400">Atualize seus dados de contato</CardDescription>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleSaveProfile} className="space-y-6">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="fullName" className="text-slate-200">
                      Nome Completo
                    </Label>
                    <Input
                      id="fullName"
                      type="text"
                      placeholder="João Silva"
                      value={formData.full_name}
                      onChange={(e) => handleInputChange("full_name", e.target.value)}
                      className="border-slate-600 bg-slate-800 text-white placeholder-slate-500"
                    />
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="city" className="text-slate-200">
                      Cidade
                    </Label>
                    <Input
                      id="city"
                      type="text"
                      placeholder="São Paulo"
                      value={formData.city}
                      onChange={(e) => handleInputChange("city", e.target.value)}
                      className="border-slate-600 bg-slate-800 text-white placeholder-slate-500"
                    />
                  </div>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="countryCode" className="text-slate-200">
                      Código do País
                    </Label>
                    <Input
                      id="countryCode"
                      type="text"
                      placeholder="+55"
                      value={formData.country_code}
                      onChange={(e) => handleInputChange("country_code", e.target.value)}
                      className="border-slate-600 bg-slate-800 text-white placeholder-slate-500"
                    />
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="whatsapp" className="text-slate-200">
                      Número de WhatsApp
                    </Label>
                    <Input
                      id="whatsapp"
                      type="tel"
                      placeholder="11999999999"
                      value={formData.whatsapp_number}
                      onChange={(e) => handleInputChange("whatsapp_number", e.target.value)}
                      className="border-slate-600 bg-slate-800 text-white placeholder-slate-500"
                    />
                  </div>
                </div>

                {error && (
                  <div className="flex gap-2 rounded-lg bg-red-500/10 p-3 text-sm text-red-400">
                    <AlertCircle className="h-4 w-4 shrink-0 mt-0.5" />
                    {error}
                  </div>
                )}

                {successMessage && (
                  <div className="flex gap-2 rounded-lg bg-emerald-500/10 p-3 text-sm text-emerald-400">
                    <Check className="h-4 w-4 shrink-0 mt-0.5" />
                    {successMessage}
                  </div>
                )}

                <Button
                  type="submit"
                  disabled={saving}
                  className="bg-gradient-to-r from-blue-500 to-emerald-500 text-white hover:from-blue-600 hover:to-emerald-600"
                >
                  {saving ? "Salvando..." : "Salvar Perfil"}
                </Button>
              </form>
            </CardContent>
          </Card>

          {/* Preferences Form */}
          <Card className="border-slate-700 bg-slate-900">
            <CardHeader>
              <CardTitle className="text-xl text-white">Preferências de Emprego</CardTitle>
              <CardDescription className="text-slate-400">
                Defina as áreas e países onde deseja receber oportunidades
              </CardDescription>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleSavePreferences} className="space-y-6">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="jobArea" className="text-slate-200">
                      Área de Trabalho ou Cargo
                    </Label>
                    <Input
                      id="jobArea"
                      type="text"
                      placeholder="ex: Desenvolvedor Full Stack"
                      value={preferences.job_area}
                      onChange={(e) => handlePreferenceChange("job_area", e.target.value)}
                      className="border-slate-600 bg-slate-800 text-white placeholder-slate-500"
                    />
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="country" className="text-slate-200">
                      País
                    </Label>
                    <select
                      id="country"
                      value={preferences.country}
                      onChange={(e) => handlePreferenceChange("country", e.target.value)}
                      className="w-full px-3 py-2 bg-slate-800 border border-slate-600 rounded-md text-white focus:outline-none focus:ring-2 focus:ring-blue-500"
                    >
                      <option value="">Selecione um país</option>
                      {COUNTRIES.map((country) => (
                        <option key={country} value={country}>
                          {country}
                        </option>
                      ))}
                    </select>
                  </div>
                </div>

                {error && (
                  <div className="flex gap-2 rounded-lg bg-red-500/10 p-3 text-sm text-red-400">
                    <AlertCircle className="h-4 w-4 shrink-0 mt-0.5" />
                    {error}
                  </div>
                )}

                {successMessage && (
                  <div className="flex gap-2 rounded-lg bg-emerald-500/10 p-3 text-sm text-emerald-400">
                    <Check className="h-4 w-4 shrink-0 mt-0.5" />
                    {successMessage}
                  </div>
                )}

                <Button
                  type="submit"
                  disabled={saving}
                  className="bg-gradient-to-r from-blue-500 to-emerald-500 text-white hover:from-blue-600 hover:to-emerald-600"
                >
                  {saving ? "Adicionando..." : "Adicionar Preferência"}
                </Button>
              </form>
            </CardContent>
          </Card>

          {/* Back Button */}
          <div>
            <Button onClick={() => router.push("/dashboard")} className="bg-slate-700 text-white hover:bg-slate-600">
              Voltar ao Dashboard
            </Button>
          </div>
        </div>
      </div>
    </div>
  )
}
